<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>系统管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/users' }">角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo.username" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addUserVisible=true">添加用户</el-button>
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :data="userList"
          border
          :cell-style="rowClass"
          :header-cell-style="headClass"
          :row-style="rowStyle"
          @selection-change="handleSelectionChange"
          style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="序号"
            type="index"
            width="50">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username"
            width="180">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile">
        </el-table-column>
        <el-table-column
            label="邮箱"
            prop="email"
        >
        </el-table-column>
        <el-table-column
            label="角色"
            prop="status"
            width="160px"
        >
          <template slot-scope="scope">
            <el-switch
                v-model=scope.row.status
                :active-value=1
                :inactive-value=0
                active-text="启用"
                inactive-text="禁用"
                @change="changeStatus(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
            label="创建时间"
            prop="createTime">
        </el-table-column>
        <el-table-column
            label="住址"
            prop="address">
        </el-table-column>
        <el-table-column
            label="操作"
            prop="salt"
        >
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary" @click="editUser(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="removeUserItem(scope.row.userId)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="分配角色" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-setting" size="mini" type="primary"
                         @click="assignRoles(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[6,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>
    <!--添加用户dialog对话框-->
    <el-dialog
        :visible.sync="addUserVisible"
        title="添加用户"
        width="35%"
        @close="closeAddUserDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addUserFormRef" :model="user" :rules="addUserFormRules" class="demo-user" label-width="100px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="user.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="user.password" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="user.mobile"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="status">
          <el-radio-group v-model="user.status">
            <el-radio :label="0">禁用</el-radio>
            <el-radio :label="1">用户</el-radio>
            <el-radio :label="2">管理员</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="住址" prop="address">
          <el-input v-model="user.address"></el-input>
        </el-form-item>
        <el-form-item label="选择时间：" prop="createTime">
          <el-date-picker
              style="float: left"
              prop="createTime"
              v-model="user.createTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="addUserVisible = false">取 消</el-button>
    <el-button type="primary" @click="addUser">确 定</el-button>
    </el-dialog>
    <!--修改用户dialog对话框-->
    <el-dialog
        :visible.sync=editUserVisible
        title="修改用户"
        width="35%"
        @close="closeEditUserDialog"
    >
      <!--这是内容区域-->
      <el-form ref="editUserFormRef" :model="user" :rules="editUserFormRules" class="demo-user" label-width="100px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="user.username"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="user.mobile"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="status">
          <el-input v-model="user.status"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="address">
          <el-input v-model="user.address"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="address">
          <el-date-picker
              style="float: left"
              v-model="user.createTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="editUserVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateUser">确 定</el-button>
    </el-dialog>
    <!-- 分配用户角色对话框-->
    <el-dialog
        :visible.sync=assignRolesVisible
        title="分配角色"
        width="35%"
    >
      <!--这是内容区域-->
      <el-radio-group v-model="user.status">
        <el-radio :label="0">禁用</el-radio>
        <el-radio :label="1">用户</el-radio>
        <el-radio :label="2">管理员</el-radio>
      </el-radio-group>
      <el-form ref="assignRolesFormRef" :model="user" class="demo-user" label-width="100px">
        <el-form-item label="角色" prop="status" style="display: inline-block">
          <el-input v-model="user.status" style="float:left; width:250px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="assignRolesVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateRolesStatus">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "User",
  data() {
    //验证邮箱的规则
    const checkEmail = (rule, value, cb) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的邮箱'))
    };
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      tableChecked: [],//被选中的记录数据-----对应“批量删除”传的参数值
      ids: [],//批量删除id
      //  请求用户列表的参数
      queryInfo: {
        username: '',
        pageNum: 1,
        pageSize: 6,
      },
      //用户列表数据
      userList: [],
      //当前数据总数
      total: 0,
      //修改或修改用户的信息
      user: {
        userId: '',
        username: '',
        password: '',
        email: '',
        mobile: '',
        status: '',
        createUserId: '',
        createTime: ''
      },
      //控制添加用户对话框是否显示
      addUserVisible: false,
      //添加用户对话框验证规则
      addUserFormRules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min: 2, max: 6, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
        email: [
          {validator: checkEmail, trigger: 'blur'}
        ],
        status: [
          {required: true, message: '请输入当前角色', trigger: 'blur'},
        ],
        createUserId: [
          {required: true, message: '请输入创建者Id', trigger: 'blur'},
          {min: 3, max: 9, message: '长度在 3 到 9 个字符', trigger: 'blur'}
        ],
      },
      //控制修改用户对话框是否显示
      editUserVisible: false,
      //修改用户对话框验证规则
      editUserFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
        email: [
          {validator: checkEmail, trigger: 'blur'}
        ],
      },
      //控制分配角色对话框是否显示
      assignRolesVisible: false,
    }
  },
  methods: {
    rowStyle({row}) {
      if (row.status === 0) {
        return {
          backgroundColor: '#D7DBDD',
        }
      }
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    handleSelectionChange(val) {
      this.tableChecked = val;
    },
    batchDelete(rows) {
      const status = rows[0].status;
      rows.forEach((item, index) => {
        this.ids.push(item.userId);
      });
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$axios.post('/admin/batchDeleteEgressUser?status=' + status, this.ids).then(res => {
          if (res.data.status !== 10000) {
            this.$message.error("删除失败！");
          }
          this.$message({type: 'success', message: '删除成功!'});
          this.getUserList();
          //   });
          console.log('删除了', this.ids);
          this.ids = [];
        }).catch((e) => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      })
    },
    //请求用户列表数据
    getUserList() {
      this.$axios.get('/admin/user/userList', {params: this.queryInfo}).then(res => {
        // console.log(res)
        //如果没有搜索,则得到用户列表
        if (res.data.data.total !== undefined) {
          if (res.data.status === 10000) {
            this.total = res.data.data.total;
            this.userList = res.data.data.list;
            this.$message.success("请求用户列表成功！")
          } else {
            this.$message.error("请求用户列表失败！")
          }
        } else {
          //如果提交了单个username，就实现搜索功能
          if (res.data.status === 10000) {
            this.total = 1;
            this.userList = [];
            this.userList.push(res.data.data)
            this.$message.success("搜索用户成功！");
          } else {
            this.$message.error("搜索用户失败！")
          }
        }
      })
    },
    //当每页数据条数发生改变的时候触发
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getUserList();
    },
    //在数据发生改变的时候触发
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getUserList();
    },
    //修改当前用户的角色
    changeStatus(row) {
      this.user = row;
      this.$axios.post('/admin/user/update', this.user).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("更新用户角色成功！");
        } else {
          this.$message.error("更新用户角色失败！");
        }
      })
    },
    //点击添加用户
    addUser() {
      this.$refs["addUserFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/user/add', this.user).then(res => {
            //关闭对话框
            if (res.data.status === 10000) {
              this.addUserVisible = !this.addUserVisible;
              this.$message.success("新增用户成功！");
              //刷新列表
              this.getUserList();
            } else {
              this.addUserVisible = !this.addUserVisible;
              this.$message.error("新增用户失败！");
            }
          })
        }
      })
    },
    //关闭添加用户对话框
    closeAddUserDialog() {
      //重置表单
      this.$refs["addUserFormRef"].resetFields();
    },
    //编辑用户信息
    editUser(row) {
      //可以不用再发送一次请求
      this.user = row;
      this.editUserVisible = !this.editUserVisible;
    },
    //确定提交修改用户信息
    updateUser() {
      this.$refs["editUserFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/admin/user/update', this.user).then(res => {
            this.$message.success("修改用户信息成功！");
            this.getUserList();
            this.editUserVisible = !this.editUserVisible;
          })
        }
      })
    },
    //关闭编辑用户对话框
    closeEditUserDialog() {
      //重置表单
      this.$refs.editUserFormRef.resetFields();
    },
    //删除用户
    removeUserItem(userId) {
      console.log(userId)
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //点击确定，发起删除用户
        // console.log(userId)
        this.$axios.post('/admin/user/delete/' + userId).then(res => {
          console.log(res);
          this.$message.success("删除用户成功！")
          this.getUserList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //点击分配角色
    assignRoles(row) {
      this.user = row;
      this.assignRolesVisible = !this.assignRolesVisible;
    },
    //确认提交用户分配角色
    updateRolesStatus() {
      this.$axios.post('/admin/user/update', this.user).then(res => {
        // console.log(res);
        this.assignRolesVisible = !this.assignRolesVisible;
        this.getUserList();
      })
    },
  },
  created() {
    //进页面之前就获取用户列表
    this.getUserList();
  }
}
</script>

<style scoped>

</style>
